import axios from 'axios';
import { Message } from 'element-ui';

/**
 * http请求工具类
 *
 * 请求拦截器 负责将客户端标识token值存储并放置在头部提交给服务端
 *
 * 响应拦截器 负责全局处理业务请求的网络或者业务错误
 */

// 创建axios的实例
if (process.env.NODE_ENV == 'development') {//开发环境
  axios.defaults.baseURL = '/api/';
} else if (process.env.NODE_ENV == 'production') {//生产环境
  axios.defaults.baseURL = 'https://ssp.zhongnangroup.cn/';
}
const service = axios.create({
  timeout: 30000, // 超时时间
});

// 请求拦截
service.interceptors.request.use(
  config => {
    if (localStorage.tsToken) {
      config.headers.Authorization = localStorage.tsToken;
    }
    return config;
  },
  err => {
    Promise.reject(err);
  }
);

service.interceptors.request.use(function (config) {
  if (localStorage.getItem("user-token")) {
    config.headers['token'] = localStorage.getItem("user-token");
  }
  // if (localStorage.getItem("access-token")) {
  //   config.headers['access-token'] = localStorage.getItem('access-token') || '';
  // }
  // config.headers['plat-form'] = 2;
  return config
})

// 响应拦截
service.interceptors.response.use(
  response => {
    return response;
  },
  err => {
    let errMsg = '';
    if (err && err.response.status) {
      switch (err.response.status) {
        case 401:
          errMsg = '登录状态失效，请重新登录';
          break;
        case 403:
          errMsg = '拒绝访问';
          break;

        case 408:
          errMsg = '请求超时';
          break;

        case 500:
          errMsg = '服务器内部错误';
          break;

        case 501:
          errMsg = '服务未实现';
          break;

        case 502:
          errMsg = '网关错误';
          break;

        case 503:
          errMsg = '服务不可用';
          break;

        case 504:
          errMsg = '网关超时';
          break;

        case 505:
          errMsg = 'HTTP版本不受支持';
          break;

        default:
          errMsg = err.response.data.msg;
          break;
      }
    } else {
      errMsg = err;
    }

    Message.error(errMsg);
    return Promise.reject(errMsg);
  }
);

export default service;
