<template>
  <div class="box">
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="项目名称" prop="name">
        <el-autocomplete
          v-model="form.name"
          :fetch-suggestions="querySearch"
          placeholder="请输入内容"
          :trigger-on-focus="false"
          @select="handleSelect"
          clearable
        ></el-autocomplete>
      </el-form-item>
      <el-form-item label="员工工号" prop="memberId">
        <el-input v-model="form.memberId" clearable></el-input>
      </el-form-item>
      <el-form-item label="条线" prop="region">
        <el-select v-model="form.region" placeholder="请选择条线">
          <el-option label="生产" value="生产"></el-option>
          <el-option label="技术" value="技术"></el-option>
          <el-option label="安全" value="安全"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">录入</el-button>
        <el-button @click="resetForm">清空</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "@/assets/index";
export default {
  name: "Form",
  data() {
    // const checkMemberId = (rule, value, callback) => {
    //   if (!value) {
    //     return callback(new Error("工号不能为空"));
    //   }
    //   axios.get('/api/temporary/person/get',{
    //       params:{
    //           staffcode: value
    //       }
    //   }).then(res => {
    //       const {data} = res;
    //     callback();
    // })
    // };
    // const checkProject = (rule, value, callback) => {
    //   if (!value) {
    //     this.$message.error("请输入项目名称");
    //     return callback(new Error("请输入项目名称"));
    //   } else if (!this.form.projectId) {
    //     this.$message.error("请选择一个项目");
    //     return callback(new Error("请选择一个项目"));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      form: {
        name: "",
        projectId: "",
        memberId: "",
        region: "",
      },
      rules: {
        name: [{ required: true, message: "请输入项目名称", trigger: "blur" }],
        memberId: [{ required: true, message: "请输入工号", trigger: "blur" }],
        region: [{ required: true, message: "请选择条线", trigger: "change" }],
      },
    };
  },
  methods: {
    onSubmit() {
      if (!this.form.projectId) {
        this.$message.error("请选择一个项目");
        return
      }
      const refform = this.$refs["form"];
      refform.validate((valid) => {
        if (valid) {
          axios
            .post("/api/temporary/tiaoxian/save", {
              project_name: this.form.name,
              project_id: this.form.projectId,
              staffcode: this.form.memberId,
              type: this.form.region,
            })
            .then((res) => {
              console.log(res.data);
              const { code } = res.data;
              if (code == 0) {
                this.$message("录入成功");
                refform.resetFields();
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["form"].resetFields();
    },
    querySearch(queryString, cb) {
      this.form.projectId = "";
      axios
        .get("/api/temporary/project/search", {
          params: {
            keyword: queryString,
          },
        })
        .then((res) => {
          const { data } = res;
          if (data.code == 0) {
            let results = data.data.map((v) => {
              v.value = `${v.name}`;
              return v;
            });
            // 调用 callback 返回建议列表的数据
            console.info(results);
            cb(results);
          }
        });
    },
    handleSelect(item) {
      console.log(item);
      this.form.name = item.name;
      this.form.projectId = item.id;
    },
  },
};
</script>

<style scoped>
.box >>> .el-form {
  width: 600px;
  border: 1px solid #ededed;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 1px 1px 20px 10px #ededed;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.box >>> .el-autocomplete {
  width: 100%;
}
</style>
